

#app{
    margin: 2rem auto;
    max-width: 90%;
    min-height: 75vh;
    width: 1000px;
    text-align: center;
}


.header{
    margin: 2rem auto;
    max-width: 90%;
    width: 1000px;
}

.nav-list{
    display: flex;
    justify-content: space-evenly;
}

.footer{
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin: 2rem auto;
    max-width: 90%;
    width: 900px;
}

.social-list{
    display: flex;
    justify-content: space-around;
    width: 50%;

}

.books-list{
    align-items: center;
    display: flex;
    flex-direction: column;
}

